import React, { useState, useRef, useEffect } from "react"
import * as THREE from "three"
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls"
import { GLTFLoader } from "three/examples/jsm/loaders/GLTFLoader"
import { Canvas, extend, useThree, useRender } from "react-three-fiber"
import {styles} from '../styles/modelWatcher.module.scss'
import {Link} from "gatsby";
import {MODEL_CUSTOMIZE, ROUTES} from "../config";
import {navigate} from "gatsby-link";
import {BackBtn} from "../components/backBtn";


extend({ OrbitControls })

const FileLoader = ({modelName}) => {
  const [model, setModel] = useState()

  useEffect(() => {
    new GLTFLoader().load(`/models/${modelName}/scene.gltf`, setModel)
  })

  return model ? <primitive  object={model.scene} /> : null
}

const Controls = () => {
  const orbitRef = useRef()
  const { camera, gl } = useThree()

  useRender(() => {
    orbitRef.current.update()
  })

  return (
    <orbitControls
      autoRotate
      maxPolarAngle={Math.PI / 3}
      minPolarAngle={Math.PI / 3}
      args={[camera, gl.domElement]}
      ref={orbitRef}
    />
  )
}



const ModelPage =  ({location}) => {

  const modelName = location.search.replace('?','')

  const isBrowser = typeof window !== "undefined"
   if(!isBrowser) return <>  model watcher page</>

  if(modelName.length<1){
    navigate(ROUTES.gallery)
    return <>  model non found  </>
  }
  return (
    <section className={styles}>
      <BackBtn  link={ROUTES.gallery}/>
      {isBrowser && (
        <Canvas
          camera={{ position:MODEL_CUSTOMIZE[modelName]?.position|| [0, 0, 4] }}
          onCreated={({ gl }) => {
            gl.shadowMap.enabled = true
            gl.shadowMap.type = THREE.PCFSoftShadowMap
          }}
        >
          <ambientLight intensity={0.5} />
          <spotLight position={[15, 20, 8]} penumbra={1} castShadow />
          {/*<fog attach="fog" args={["blue", 10, 25]} />*/}
          <Controls />
          <FileLoader  modelName={modelName}/>
        </Canvas>
      )}
    </section>
  )
}

export default ModelPage